.nav {
    padding: 20px;
}

.item {
    margin-top: 15px;
}

.item a {
    color: #07283d;
    font-size: 20px;
    text-decoration: none;
}

.item a.activeLink {
    color: #0f1e3d;
}

.item a:hover {
    background-color: #4682B4;
    color: white;
    cursor: pointer;
    transition: width 2s, background-color 1s, height 4s; /* плаыный переход*/
}

